@-webkit-keyframes FadeIn{
0%{-webkit-transform:scale(0.1) translateX(520px);transform:scale(0.1) translateX(520px);opacity:0
}
25%{-webkit-transform:scale(0.5) translateX(360px);transform:scale(0.5) translateX(360px);opacity:0.25
}
50%{-webkit-transform:scale(0.5) translateX(130px);transform:scale(0.5) translateX(130px);opacity:0.5
}
75%{-webkit-transform:scale(0.5) translateX(15px);transform:scale(0.5) translateX(15px);opacity:0.75
}
100%{-webkit-transform:scale(1) translateX(0);transform:scale(1) translateX(0);opacity:1
}
}
@keyframes FadeIn{
0%{-webkit-transform:scale(0.1) translateX(520px);transform:scale(0.1) translateX(520px);opacity:0
