100%{-webkit-transform:scale(1) translateX(0);transform:scale(1) translateX(0);opacity:1
}
}
@-webkit-keyframes FadeOut{
0%{-webkit-transform:scale(0.1) translateX(0);transform:scale(0.1) translateX(0);opacity:1
