}
100%{-webkit-transform:scale(1) translateX(-520px);transform:scale(1) translateX(-520px);opacity:0
}
}
@keyframes FadeOut{
0%{-webkit-transform:scale(0.1) translateX(0);transform:scale(0.1) translateX(0);opacity:1
}
25%{-webkit-transform:scale(0.5) translateX(-15px);transform:scale(0.5) translateX(-15px);opacity:0.75
